@import "../../../styles/vars";
@import "../../../styles/mixins";

.errorBlock {
  padding-top: 180px;
  padding-bottom: 80px;
  @include flexCol(flex-start, flex-start);
  column-gap: 20px;

  @include xxlScreenMedia {
    padding-top: 160px;
  }

  @include mdScreenMedia {
    flex-direction: column;
    row-gap: 30px;
  }

  @include xlScreenMedia {
    padding-top: 140px;
    padding-bottom: 60px;
  }

  @include lgScreenMedia {
    padding-top: 120px;
    padding-bottom: 30px;
  }


  @include smScreenMedia {
    padding-top: 78px;
    padding-bottom: 0px;
    row-gap: 0;
  }

  &__texts {
    max-width: 495px;
    width: 100%;
    margin-top: 87px;

    @include xxlScreenMedia {
      max-width: 435px;
      margin-top: 36px;
    }

    @include xlScreenMedia {
      max-width: 350px;
    }

    @include lgScreenMedia {
      margin-top: 15px;
    }

    @include mdScreenMedia {
      max-width: 500px;
    }

    @include smScreenMedia {
      max-width: 270px;
    }
  }

  &__title {
    margin-bottom: 15px;
    color: $color-white;
  }

  &__contentText {
    margin-bottom: 30px;
    color: $color-white;
    @include setFont(20px, 500);

    @include xxlScreenMedia {
      font-size: 18px;
    }

    @include xlScreenMedia {
      font-size: 16px
    }

    @include smScreenMedia {
      display: none;
    }
  }

  &__btn {
    &_desk {
      @include smScreenMedia {
        display: none;
      }
    }

    &_mob {
      display: block;
      margin-left: auto;
      margin-right: auto;
      margin-top: 30px;
      @media (min-width: 577px){
        display: none;
      }
    }
  }

  &__img {
    width: 100%;

    &_desk {
      @include mdScreenMedia {
        display: none;
      }
    }

    &_mob {
      @media (min-width: 769px){
        display: none;
      }
    }
  }

}
